import React, { useState } from 'react';
import PedidoForm from './componentes/PedidoForm';
import TablaPedidos from './componentes/PedidoTabla';
import './style.css';
import './boton-agregar.css';
import './boton-eliminar.css';
import './boton-editar.css'
import 'bootstrap/dist/css/bootstrap.min.css';
import CocktailList from './componentes/CocktailList';


const App = () => {
  const [pedidos, setPedidos] = useState([]);
  const [pedidoEditado, setPedidoEditado] = useState(null);

  const agregarPedido = (mesa, producto, cantidad) => {
    // Lógica para agregar un nuevo pedido a la tabla
    const nuevoPedido = {
      id: Date.now(),
      mesa,
      producto,
      cantidad,
      precio: obtenerPrecioProducto(producto), // Función para obtener el precio según el producto seleccionado
    };

    setPedidos([...pedidos, nuevoPedido]);
  };

  const editarPedido = (pedido) => {
    // Lógica para editar un pedido existente
    setPedidoEditado(pedido);
  };

  const actualizarPedido = (mesa, producto, cantidad) => {
    // Lógica para actualizar el pedido editado
    const pedidosActualizados = pedidos.map((pedido) => {
      if (pedido.id === pedidoEditado.id) {
        return {
          ...pedido,
          mesa,
          producto,
          cantidad,
        };
      }
      return pedido;
    });

    setPedidos(pedidosActualizados);
    setPedidoEditado(null);
  };

  const eliminarPedido = (pedidoId) => {
    // Lógica para eliminar un pedido de la tabla
    const pedidosActualizados = pedidos.filter(
      (pedido) => pedido.id !== pedidoId
    );
    
    setPedidos(pedidosActualizados);
  };
  const obtenerPrecioProducto = (producto) => {
    switch (producto) {
      case 'Pizza':
        return 2500;
      case 'Hamburguesa':
        return 1400;
      case 'Tostado Jamón y Queso':
        return 700;
      case 'Sándwich de Milanesa':
        return 1700;
      default:
        return 0;
    }
  };
 return (
    <div className="mi-div">
      <h1>Vanille Restaurant</h1>
      <PedidoForm
        agregarPedido={agregarPedido}
        editarPedido={actualizarPedido}
        pedidoEditado={pedidoEditado}
      />
      <TablaPedidos
        pedidos={pedidos}
        editarPedido={editarPedido}
        eliminarPedido={eliminarPedido}
      />
      <CocktailList />
    </div>
  
  );
};

export default App;
