import { Button } from 'bootstrap';
import React, { useState, useEffect } from 'react';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';

const PedidoForm = ({ agregarPedido, editarPedido, pedidoEditado }) => {
  const [mesa, setMesa] = useState('');
  const [producto, setProducto] = useState('');
  const [cantidad, setCantidad] = useState('');

  useEffect(() => {
    if (pedidoEditado) {
      setMesa(pedidoEditado.mesa);
      setProducto(pedidoEditado.producto);
      setCantidad(pedidoEditado.cantidad);
    } else {
      setMesa('');
      setProducto('Pizza'); // Seleccionamos "Pizza" por defecto
      setCantidad('');
    }
  }, [pedidoEditado]);

  const handleSubmit = (e) => {
    e.preventDefault();

    if (pedidoEditado) {
      editarPedido(mesa, producto, cantidad);
    } else {
      agregarPedido(mesa, producto, cantidad);
    }

    setMesa('');
    setProducto('Pizza');
    setCantidad('');
  };

  return (
      <Form onSubmit={handleSubmit}>
       <Form.Group as={Row} className="mb-3" controlId="NumMesa"> <label>

       <div id='numero-de-mesa'>
        Número de Mesa:
        <input
          type="text"
          value={mesa}
          onChange={(e) => setMesa(e.target.value)}
        />
        </div>

      </label>
      <label>
          <div id='producto'>
          Producto:
          <select
          value={producto}
          onChange={(e) => setProducto(e.target.value)}
        >
            <option value="Pizza">Pizza</option>
            <option value="Hamburguesa">Hamburguesa</option>
            <option value="Tostado Jamón y Queso">Tostado Jamón y Queso</option>
            <option value="Sándwich de Milanesa">Sándwich de Milanesa</option>
            </select>
          </div>
      </label>
      <label>
        <div id='cantidad'>
        Cantidad:
        <input
          type="number"
          value={cantidad}
          onChange={(e) => setCantidad(e.target.value)}
        />
        </div>
      </label>
      <div class="contenedor-botones"><br></br><br></br>
      <button type="submit" class='boton cinco'>
      <div class="icono">
					<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-right" viewBox="0 0 16 16">
						<path fill-rule="evenodd" d="M1 8a.5.5 0 0 1 .5-.5h11.793l-3.147-3.146a.5.5 0 0 1 .708-.708l4 4a.5.5 0 0 1 0 .708l-4 4a.5.5 0 0 1-.708-.708L13.293 8.5H1.5A.5.5 0 0 1 1 8z"/>
					  </svg>
				</div>
        <span>{pedidoEditado ? 'Aceptar' : 'Agregar'}</span>
        </button>
        </div></Form.Group>
      </Form>
  );
};

export default PedidoForm;
